@charset "UTF-8";

/* Light theme colors */
$light-background: #F5E8C7;
$light-background-secondary: #DEB6AB;
$light-background-header: #FFDEB4;
$light-background-hover: #E94560;
$light-color: #5E454B;
$light-color-secondary: #ECCCB2;
$light-color-tertiary: #FFB4B4;
$light-color-emphasis: #268BD2;
$light-color-hover: #FFF8BC;
$light-border-color: #FFDEB4;
$light-table-color: #FFF9CA;

/* Dark theme colors */
$dark-background: #002B36;
$dark-background-secondary: #586E75;
$dark-background-header: #073642;
$dark-background-hover: #CB4B16;
$dark-color: #93A1A1;
$dark-color-secondary: #382933;
$dark-color-tertiary: #C4C4CD;
$dark-color-emphasis: #E0775A;
$dark-color-hover: white;
$dark-border-color: #4E4E57;
$dark-table-color: #4E4E57;

/* Section colors */
$section-home-color: #B5240B;
$section-project-color: #D33682;
$section-art-color: #6C71C4;
$section-log-color: #2AA198;
$section-unknown-color: #b58900;

$underline-opacity: 0.6;

$media-size-phone: "(max-width: 684px)";
$media-size-tablet: "(max-width: 900px)";

/* Variables for js, must be the same as these in @custom-media queries */
:root {
  --phoneWidth: (max-width: 684px);
  --tabletWidth: (max-width: 900px);
}

/* Content */
$max-width: 860px;
