.footer {
  display: flex;
  padding: 40px 20px;
  flex-grow: 0;
  flex-wrap: wrap;
  justify-content: space-around;

  [data-theme=dark] & {
    background: $dark-background;
  }

  [data-theme=light] & {
    background: $light-background;
  }

  &__inner {
    display: flex;
    align-items: center;
    justify-content: center;
    //width: 760px;
    max-width: 100%;

    @media #{$media-size-tablet} {
      flex-direction: column;
    }
  }

  &__content {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: 1rem;

    @media #{$media-size-tablet} {
      width: 760px;
      margin-top: 10px;
    }

    & > *:not(:last-child)::after {
      content: "|";
      padding: 0 5px;
    }

    & > *:last-child {
      padding: 0 0px;
    }

    & a {
      text-decoration: none;
      padding: 6px;
    }
  }

  .icon {
    margin: 0 auto;
    vertical-align: middle;
    width: 18px;
    height: 18px;
  }

  .email {
    padding: 0px;
    margin: 0px;
  }
}
