.homepage {
  &-content {
    width: 100%;
    max-width: 800px;
    text-align: left;
    padding: 20px;
    margin: 20px auto;

    @media #{$media-size-tablet} {
      max-width: 660px;
    }

    & ul {
        columns: 2;
        @media #{$media-size-tablet} {
            columns: 1;
        }
    }
  }

  &-header {
    display: flex;
    width: 100%;
    max-width: 1200px;
    flex-flow: row nowrap;
    justify-content: center;

    @media #{$media-size-tablet} {
      flex-flow: row wrap;
      max-width: #{$media-size-tablet};
    }
  }

  &-title {
    font-family: 'EB Garamond', Times, 'Times New Roman', Georgia, serif;
    font-size: 4.2rem;
    margin: 0.5em;
    padding: 10px;
    min-height: 1.5em;
    line-height: 1;
    text-align: right;

    @media #{$media-size-tablet} {
      font-size: 3.8rem;
      text-align: center;
    }
  }

  &-subtitle {
    font-family: 'Inconsolata', Consolas, Monaco, Andale Mono, Ubuntu Mono, monospace;
    font-size: 2rem;
    margin: 0.5em;
    padding: 10px;
    min-height: 2.5em;
    max-width: 100vw;
    line-height: 1;
    text-align: right;
    color: #41FF00;
    background-color: #111111;
    border-radius: 20px;
    white-space: nowrap;
    overflow-x: hidden;

    @media #{$media-size-tablet} {
      font-size: 1.5rem;
    }

    &::before {
      display: inline-block;
      content: ">";
      margin-right: 8px;
    }
  }

  &-intro {
    flex-basis: 175%;
  }

  &-avatar {
    padding: 20px;
    display: flex;
    justify-content: center;
    flex-basis: 75%;
    flex-shrink: 1.2;

    & img {
      aspect-ratio: 1;
      align-self: center;
      border-radius: 100%;
    }
  }

  &-links {
    display: flex;
    justify-content: flex-end;
    flex-flow: row wrap;

    @media #{$media-size-tablet} {
      justify-content: space-around;
    }
    
    a {
      margin: 10px;
      padding: 20px;
      display: flex;
      align-items: center;
      text-decoration: none;
      font-size: 1.5rem;
      font-weight: 800;
    }
  }

  &-experience {
      margin: 50px 10px 50px 10px;
  }
}

.carousel-cell {
  margin: 0;
  padding: 0;
  color: white;
  width: 100%;

  &:nth-child(odd) {
    background: red;
  }
  &:nth-child(even) {
    background: blue;
  }
}

.beaver {
  color: #DC4405;
  font-weight: bold;
  text-shadow: 2px 2px 1px black;
}

#retypewrite {
  display: flex;
  justify-content: flex-end;

  &::after {
    display: inline-block;
    content: "";
    margin-left: 5px;
    border-radius: 1px;
    min-width: 6px;
    width: 6px;
    height: 1em;
    background: #41FF00;
    animation: cursor 1s infinite;
  }
}

