.error-404 {
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    overflow: auto;
    width: 100%;
    height: 100%;
    background: black;
    z-index: 10000;
    font-family: 'Inconsolata', Consolas, Monaco, Andale Mono, Ubuntu Mono, monospace;
    font-display: auto;
    font-feature-settings: normal;
    line-height: 1;

    &-amiga {
        max-width: 800px;
        text-align: center;
        padding: 10px 20px 10px 20px;
        font-size: 24px;
        color: red;
    }

    p {
        font-family: inherit;
        font-size: inherit;
        font-weight: bold;
        line-height: inherit;
        margin: 10px;
    }

    a {
        text-decoration: none;
    }
}

@keyframes amigaBorder {
    from, to {
        border-color: transparent
    }
    50% {
        border-color: red
    }
}

.amigaBorder {
    border: 8px solid red;
    animation: amigaBorder 1.2s step-end infinite;
}
