.logo {
  display: flex;
  flex-shrink: 0;
  flex-wrap: nowrap;
  overflow: visible;
  align-items: center;
  text-decoration: none;
  font-size: 1.4rem;
  font-weight: bold;
  font-display: auto;
  font-family: 'Inconsolata', Courier, monospace;

  a {
    text-decoration: none;
    padding: 5px 0px 5px 0px;

    [data-theme=dark] & {
      color: $dark-color;
    }

    [data-theme=light] & {
      color: $light-color;
    }
  }

  & * {
    display: inline-block;
  }

  &_mark {
    margin-right: 8px;
    font-size: 1.5rem;
  }

  &_type {
    color: $section-unknown-color;
  }

  &_separator {
    padding: 0 4px 0 4px;
    opacity: 0.7;

    & * {
      vertical-align: middle;
    }
  }

  &_anti {
    display: none;
  }

  &_cursor {
    width: 10px;
    height: 1rem;
    background: $section-unknown-color;
    margin-left: 5px;
    border-radius: 1px;
    animation: cursor 1s infinite;
  }

  @media (prefers-reduced-motion: reduce) {
    &_cursor {
        animation: none;
    }
  }

  .cursor__page { background: $section-home-color; }
  .cursor__project { background: $section-project-color; }
  .cursor__art { background: $section-art-color; }
  .cursor__log { background: $section-log-color; }

  .logo_type {
    &.logo_type_page { color: $section-home-color; }
    &.logo_type_project { color: $section-project-color; }
    &.logo_type_art { color: $section-art-color; }
    &.logo_type_log { color: $section-log-color; }
  }

  .logo_cursor {
    &.logo_cursor_page { background: $section-home-color; }
    &.logo_cursor_project { background: $section-project-color; }
    &.logo_cursor_art { background: $section-art-color; }
    &.logo_cursor_log { background: $section-log-color; }
  }

  .logo_root {
    color: $section-home-color;
  }

  .icon {
    height: 24px;
    width: 24px;
    margin: 0 auto;
  }
}

@keyframes cursor {
  0% { opacity: 0; }
  50% { opacity: 1; }
  100% { opacity: 0; }
}
