a {
  text-decoration-style: wavy;
  text-decoration-line: underline;

  [data-theme=dark] & {
    text-decoration-color: rgba($dark-background-hover, $underline-opacity);
  }

  [data-theme=light] & {
    text-decoration-color: rgba($light-background-hover, $underline-opacity);
  }

  text-decoration-skip-ink: none;
  text-decoration-thickness: 2px;

  &:hover {
    [data-theme=dark] & {
      color: $dark-background-hover;
    }

    [data-theme=light] & {
      color: $light-background-hover;
    }
  }
}

p a, h2 a, h3 a, h4, a, aside a {
}

.footer a {
  text-decoration: none;
  background-repeat: no-repeat;
  transition: all 0.2s ease-in;
  background-size: 100% 0.2em;
  background-position: 0 90%;

  &:hover, &:focus {
    background-size: 100% 225%;
    [data-theme=dark] & {
      background: $dark-background-hover;
      color: $dark-color-hover;
    }

    [data-theme=light] & {
      background: $light-background-hover;
      color: $light-color-hover;
    }
  }
}

h1 a, .button a, .menu a, footer a {
  transition: all 0.2s ease-in;

  &:hover {
    [data-theme=dark] & {
      background: $dark-background-hover;
      color: $dark-color-hover;
    }

    [data-theme=light] & {
      background: $light-background-hover;
      color: $light-color-hover;
    }
  }
}

.homepage-links a, .sharing-buttons a {
  transition: all 0.2s ease-in;

  &:hover {
    border-radius: 100%;
    [data-theme=dark] & {
      background: $dark-background-hover;
      color: $dark-color-hover;
    }

    [data-theme=light] & {
      background: $light-background-hover;
      color: $light-color-hover;
    }
  }
}

.logo_text a {
  display: inline-block;
  vertical-align: middle;
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0,0,0,0);
  position: relative;
  overflow: hidden;

  &:before {
    content: "";
    position: absolute;
    z-index: -1;
    bottom: 0;
    background: white;
    height: 4px;
    transition-duration: 0.3s;
    transition-timing-function: ease-out;
  }

  &.hover-logo:nth-child(odd) {
    &:before {
      left: 100%;
      right: 0;
      transition-property: left;
    }

    &:hover:before, &:focus:before, &:active:before {
      left: 0;
    }
  }

  &.hover-logo:nth-child(even) {
    &:before {
      left: 0;
      right: 100%;
      transition-property: right;
    }

    &:hover:before, &:focus:before, &:active:before {
      right: 0;
    }
  }
}
